import { HorizontalScroll, TabsItem, Tabs } from "@vkontakte/vkui"
import { useState } from "react"

const months = ['янв', 'фев', 'мар', 'апр', 'мая', 'июн', 'июл', 'авг', 'сен', 'окт', 'дек',]
const days = ['пн', 'вт', 'ср', 'чт', 'пт', 'сб', 'вс',]

export const CalendarTabBar = () => {
    const [selected, setSelected] = useState(0)

    return (
        <Tabs mode="accent">
            <HorizontalScroll arrowSize="m">
                {
                    Array(30)
                        .fill(null)
                        .map((e, index) => new Date(Date.now() + index * 86400000))
                        .map((e, index) => 
                            <TabsItem
                                selected={selected == index}
                                onClick={() => setSelected(index)}
                            >
                                {`${e.getDate()} ${months[e.getMonth()]}`}
                                <br/>
                                {`${days[e.getDay()]}`}
                            </TabsItem>
                        )
                }
            </HorizontalScroll>
        </Tabs>
    )
}